import React, { useState, useEffect } from 'react';
import Footer from './Footer';
const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;
const recognition = new SpeechRecognition();
recognition.continuous = true;
recognition.interimResults = true;
recognition.lang = 'he-IL';

export default function App() {

  const [isList, setIsList] = useState(false);
  const [note, setNote] = useState(null);
  const [saveNote, setSaveNote] = useState([]);

try {
  const handleListen = () => {
    try {
      if(isList){
        recognition.start();

        recognition.onend = () => {
          console.log(`ממשיך...`);
          setTimeout(() => {
            recognition.start();
          },1)
        }
      } else {
        recognition.stop();
        recognition.onend = () => {
          console.log(`מפסיק`);
        }
      }
      recognition.onstart = () => {
        console.log(`מיקרופון פועל`);
      }
      recognition.onresult = e => {
        let current = e.resultIndex;
        let transcript = e.results[current][0].transcript;
        let mobileRepeatBug = (current === 1 && transcript === e.results[0][0].transcript);

        if (!mobileRepeatBug){
          setNote(transcript)
          console.log(transcript);
          recognition.onerror = e => console.log(e.error);
          if (transcript === 'מחק' || transcript === ' מחק'){
            setSaveNote([...saveNote]);
          }
        }

      }        
    } catch (error) {
      console.log(error);      
    }
  }
  useEffect(() => {
    handleListen()
  }, [handleListen, isList])

} catch (error) {
  console.log(error);
}
  const handleSaveNote = () => {
    setSaveNote([...saveNote, note]);
    setNote(note);
  }

  return ( <>
  <div className="container-fluid px-0 height-100 text-light text-center pt-3 offset-photo">
  <h1 className="h1"><a href="https://milim.cf" className="hp-title">וַיְדַבֵּר</a></h1>
    <div className="text-light px-5 pt-5">
        <h4 className="m-5 font-weight-light mb-2 ">תמלול בזמן אמת</h4>
          {isList ? <div> <span aria-label='img' role="img" className="text-success h1">
                    <i className="fas fa-microphone scale-btn"></i><br></br></span><span className="p">ניתן לדבר כעת...</span>
            </div> : <span aria-label='img' role="img" className=" text-danger h1">
              <i className="fas fa-microphone-slash"></i></span>}
          <div className="row p-5 mx-2 ">
          <button onClick={() => setIsList(prevState => !prevState)} className="col-xl-5 mb-1 btn btn-outline-danger py-5">
            <span className="badge badge-pill badge-secondary badge-danger px-3 ">הקלטה / עצירה</span>
          </button>
          <span className="col-2"></span>
          <button onClick={handleSaveNote } disabled={!note} className="col-xl-5 mb-1 btn btn-outline-success py-5">
            <span className="badge badge-secondary badge-success px-5 py-2">שמירה</span>
          </button>
          </div>
          <h2 className="h2">{note}</h2>

      </div>
      <div className="text-dark bg-light mx-auto ouput-size p-3">
          <h1 className="h1"><span className="mt-2 px-5 text-light badge text-secondary bg-dark">התמליל שלך: </span></h1>
          <div className="form-row">
            {saveNote.map(n => <h4 contentEditable className="px-0 h4 my-0 py-0 text-dark" key={n}>{n}<span>&#160;</span></h4>)}        
            <p class="col-12 text-muted  pt-5 mt-5 mb-0 pb-0 align-items-end">העזרו בפקודה הקולית 'מחק', כדי למחוק את התמליל השמור</p>
          </div>
      </div>
      <div className="mt-5"><Footer /></div>      
  </div>
    </>
  );
}